.home {
  header {
    width: 100%;
    height: 100vh;
    max-height: 100vh;
    background: $color-0;
    // background-image: linear-gradient(to top, #30cfd0 0%, #330867 100%);
    overflow: hidden;

    .header-container {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 90%;
      height: 100%;
      margin: 0 auto;
      position: relative;

      .content {
        width: 100%;
        max-width: 700px;
        margin: 0 auto;
        // box-shadow: 0 4px 30px 0 rgba(193, 192, 192, 0.259);
        padding: 20px;
        border-radius: 30px;
        @include glass-effect-2;
        @include simple-center;

        @media screen and (max-width: 768px) {
          width: 100%;
        }

        div {
          width: 100%;
          text-align: center;

          h1 {
            font-family: $font-3;
            font-size: 2.5rem;
            font-weight: 700;
            color: rgb(6, 192, 221);
            letter-spacing: -1px;

            @media screen and (max-width: 768px) {
              font-size: 1.7rem;
            }

            span {
              display: block;
              font-family: $font-2;
              color: rgba(20, 19, 19, 0.922);
              font-weight: 700;
              font-size: 1.7rem;
              margin-bottom: 10px;

              @media screen and (max-width: 768px) {
                font-size: 1.2rem;
              }
            }
          }

          .desc {
            width: 100%;
            margin: 10px auto;

            span {
              font-family: $font-1;
              font-size: 1.2rem;
              font-weight: 400;
              font-style: italic;
              text-align: center;
            }
          }

          .contact-btn {
            min-width: 100px;
            width: 180px;
            max-width: 200px;
            margin: 20px auto 0;
            background: white;
            border: 1px solid rgb(0, 174, 255);
            border-radius: 10px;
            transition: 0.5s ease-in-out;

            &:hover {
              color: white;
              background: rgb(0, 174, 255);
              ;
            }

            .link {
              display: block;
              padding: 9px 18px;

              @media screen and (max-width: 768px) {
                font-size: 0.9rem;
                padding: 7px 14px;
              }
            }
          }
        }
      }

      .scroll-indicator {
        position: absolute;
        width: 70px;
        height: 70px;
        bottom: -20px;
        left: 50%;
        transform: translate(-50%);
        background: rgb(255, 255, 255);
        @include glass-effect-1;
        border-radius: 150px;
        @include simple-center;

        i {
          font-size: 1.5rem;
          animation: scrollDown infinite 1s linear alternate;
          padding-bottom: 10px;
          color: $color-1;
        }

        @keyframes scrollDown {
          40% {
            transform: translateY(5px);
          }

          100% {
            transform: translate(0);
          }
        }
      }
    }
  }

  .skills,
  .projects {
    .navigation {
      display: none;
    }
  }
}